* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  padding: 7 10rem;
}

.App {
  width: 60%;
  margin: auto;
}

.collapsible {
  width: 100%;
}

.check {
  z-index: 300;
  cursor: default;
}

.flex-conteiner {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.bg-white {
  background: #fff;
}

.flex-conteiner div {
  padding: 0 2rem;
  position: relative;
}

.no-outline {
  outline: none;
}

.mgr-1 {
  margin-right: 1rem;
}

textarea {
  resize: none;
}

.mg-1 {
  margin: 0.5rem 0;
}

.priority {
  text-align: center;
  position: absolute;
  left: -2rem;
}

.inline {
  float: right;
}

.done {
  text-decoration: line-through;
}


